@import "_import";

$notosanskrm: 'Noto Sans KR Medium';
$notosanskr: 'Noto Sans KR';
$rosendale: 'roslindaletext';
$rosendaleb: 'roslindaletext-bold';
$rosendalei: 'roslindaletext-italic';
$ambit-regular: 'ambit-regular';

$title-background-color: #244735;
$title-animation-duration-pc: 35s;
$title-animation-duration-mo: 40s;

$animation-duration-1-pc: 45s;
$animation-duration-2-pc: 45s;
$animation-duration-3-pc: 30s;
$animation-duration-4-pc: 20s;


$animation-duration-1-mo: 35s;
$animation-duration-2-mo: 29s;
$animation-duration-3-mo: 30s;
$animation-duration-4-mo: 20s;

$animation-standard: marquee #{$animation-duration-4-pc} linear infinite;
$animation-reverse: marquee #{$animation-duration-4-pc} linear reverse infinite;


.event {
  $img-width : 260;
  $mass-top-pc: 54;
  $mass-top-mo: 24;

  background-color: #fff;
  padding-top: 120rem;
  max-width: 4000px;
  margin: 0 auto;
  @include full {
    padding-top: 120px;
  }
  @include mobile {
    padding-top: 80rem;
  }

  // 상단 타이틀
  &__title {
    $title-height-pc: 54rem;
    $title-height-mo: 32rem;
    position: relative;
    width: 100%;
    height: $title-height-pc;
    margin-bottom: -#{$mass-top-pc}rem;
    overflow: hidden;
    background-color: $title-background-color;
    @include mobile {
      height: $title-height-mo;
      margin-bottom: -48rem;
    }


    .mass {
      position: relative;
      width: 100%;
      height: 100%;
      &__rolling {
        position: absolute;
        white-space: nowrap;

        &--duplicate {
          .mass__rolling__contents {
            left: 100%;
            position: relative;
            top: 0;
          }
        }
        &__contents {
          position: absolute;
          top: 0;
          left: 0;
          display: inline-block;
          text-transform: uppercase;
          color: #fff;
          &.animation-on-going {
            animation: $animation-standard;
            animation-duration: $title-animation-duration-pc;
            @include mobile {
              animation-duration: $title-animation-duration-mo;
            }
          }
          p {
            display: inline-block;
            em {
              display: inline-block;
              margin: 0 27rem 0;
              font-family: $rosendalei;
              font-size: 20rem;
              line-height: $title-height-pc;
              @include mobile {
                margin: 0 14rem 0;
                font-size: 12rem;
                line-height: $title-height-mo;
              }
            }
            span {
              display: inline-block;
              font-family: $ambit-regular;
              font-size: 22rem;
              line-height: $title-height-pc;
              @include mobile {
                font-size: 13rem;
                line-height: $title-height-mo;
              }
            }
          }
        }
      }
    }
  }

  // 이벤트 리스트
  &__list {
    position: relative;
    padding: 40rem 0 200rem;
    overflow: hidden;
    @include full {
      padding: 40px 0 200px;
    }
    @include mobile {
      padding: 40rem 0 100rem;
    }
    > li {
      position: relative;
      z-index: 1;
      height: auto;
      &:nth-child(even) {
        .mass {
          &__rolling {
            &--duplicate {
              .mass__rolling__contents {
                &.animation-on-going {
                  animation: $animation-reverse;
                }
              }
            }
            &__contents {
              &.animation-on-going {
                animation: $animation-reverse;
              }
            }
          }
        }
      }

      // 이벤트 이미지 속도 제어
      &:nth-child(1) {
        .mass {
          &__rolling,
          &__rolling--duplicate {
            .mass__rolling__contents.animation-on-going {
              animation-duration: $animation-duration-1-pc;
            }
          }
        }
      }
      &:nth-child(2) {
        .mass {
          &__rolling,
          &__rolling--duplicate {
            .mass__rolling__contents.animation-on-going {
              animation-duration: $animation-duration-2-pc;
            }
          }
        }
      }
      &:nth-child(3) {
        .mass {
          &__rolling,
          &__rolling--duplicate {
            .mass__rolling__contents.animation-on-going {
              animation-duration: $animation-duration-3-pc;
            }
          }
        }
      }
      &:nth-child(4) {
        .mass {
          &__rolling,
          &__rolling--duplicate {
            .mass__rolling__contents.animation-on-going {
              animation-duration: $animation-duration-4-pc;
            }
          }
        }
      }
      @include mobile {
        &:nth-child(1) {
          .mass {
            &__rolling,
            &__rolling--duplicate {
              .mass__rolling__contents.animation-on-going {
                animation-duration: $animation-duration-1-mo;
              }
            }
          }
        }
        &:nth-child(2) {
          .mass {
            &__rolling,
            &__rolling--duplicate {
              .mass__rolling__contents.animation-on-going {
                animation-duration: $animation-duration-2-mo;
              }
            }
          }
        }
        &:nth-child(3) {
          .mass {
            &__rolling,
            &__rolling--duplicate {
              .mass__rolling__contents.animation-on-going {
                animation-duration: $animation-duration-3-mo;
              }
            }
          }
        }
        &:nth-child(4) {
          .mass {
            &__rolling,
            &__rolling--duplicate {
              .mass__rolling__contents.animation-on-going {
                animation-duration: $animation-duration-4-mo;
              }
            }
          }
        }
      }
      &:first-child {
        .mass {
          &:before {
            display: none;
          }
        }
      }
      &:last-child {
        .mass {
          &:after {
            content: '';
            position: absolute;
            bottom: 0;
            width: 100%;
            height: 4px;
            background-color: #000;
            z-index: 30;
            @include mobile {
              height: 2px;
            }
          }
        }
      }
      .mass {
        $mass: &;
        position: relative;
        width: 100%;
        height: 215rem;
        padding: #{$mass-top-pc}rem 0;
        &:before {
          content: '';
          position: absolute;
          top: 0;
          width: 100%;
          height: 4px;
          background-color: #000;
          z-index: 30;
          @include mobile {
            height: 2px;
          }
        }
        @include full {
          height: 215px;
          padding: #{$mass-top-pc}px 0;
          &:before {height: 4px;}
        }
        @include mobile {
          height: 130rem;
          padding: #{$mass-top-mo}rem 0;
        }

        &:hover {
          .mass__rolling {
            &__contents {
              &.animation-on-going {
                animation-play-state: paused;
                @include mobile {
                  animation-play-state: running;
                }
              }
            }
          }
        }
        &__rolling {
          position: absolute;
          margin: 0 auto;
          white-space: nowrap;
          &--duplicate {
            .mass__rolling__contents {
              position: relative;
              left: 100%;
              &.animation-on-going {
                animation: $animation-standard;
              }
            }
          }

          &__contents {
            position: relative;
            display: inline-block;
            &.animation-on-going {
              animation: $animation-standard;
            }
            z-index: 20;
            top: -5rem;
            @include full {
              top: -5px;
            }
            @include mobile {
              top: 0;
              &:nth-child(2) {
                display: none;
              }
            }
            @include ie-only {
              top: -15rem;
            }
            &:hover {
              .mass__image {
                right: -120rem;
                width: 520rem;
                height: 300rem;
                @include mobile {
                  width: 150rem;
                  height: 80rem;
                  right: 0;
                }
              }
            }
          }
        }
        &__text {
          padding-left: 25rem;
          @include full {
            padding-left: 25px;
          }
          @include mobile{
            padding-left: 38rem;
          }
          &__title {
            display: inline-block;
            vertical-align: top;
            font-size: 110rem;
            line-height: 1.1;
            font-family: $notosanskrm;
            @include full {
              font-size: 110px;
            }
            @include mobile {
              vertical-align: top;
              font-size: 36rem;
              line-height: 1.22;
            }
          }
          &__desc {
            $desc-padding-right-mo: 165rem;

            position: relative;
            display: inline-block;
            min-height: 110rem;
            vertical-align: top;
            margin: 6rem 0 0 25rem;
            padding-right: calc(#{$img-width * 1rem} + 92rem);
            @include full {
              min-height: 110px;
              margin: 6px 0 0 25px;
              padding-right: calc(#{$img-width * 1px} + 92px);
            }
            @include mobile {
              position: static;
              min-height: inherit;
              margin-top: 0;
              padding-right: $desc-padding-right-mo;
            }
            p {
              position: relative;
              z-index: 20;
              font-size: 24rem;
              line-height: 1.5;
              font-family: $notosanskr;
              @include full {
                font-size: 24px;
              }
              @include mobile {
                font-size: 16rem;
              }
              em {
                font-family: $rosendaleb;
              }
              strong {
                font-family: $notosanskrm;
              }
            }
            .period {
              display: block;
              margin-top: 10rem;
              font-size: 24rem;
              line-height: 1.24;
              font-family: $notosanskrm;
              em {
                font-family: $rosendaleb;
              }
              @include full {
                margin-top: 10px;
                font-size: 24px;
              }
              @include mobile {
                margin-top: 18rem;
                font-size: 14rem;
              }
            }
            a {
              position: absolute;
              bottom: 0;
              right: calc(#{$img-width * 1rem} + 92rem);
              display: inline-block;
              z-index: 20;
              @include full {
                right: calc(#{$img-width * 1px} + 92px);
              }
              @include mobile {
                width: 100%;
                height: 100%;
                text-indent: -9999px;
                &:after {
                  display: none;
                }
              }
            }

            // 모바일 자세히 보기 페이크 버튼
            .btn-diddle {
              display: none;
              @include mobile {
                display: inline-block;
                position: absolute;
                bottom: 5rem;
                right: $desc-padding-right-mo;
              }
            }
          }
        }
        &__image {
          position: absolute;
          top: 50%;
          right: 66rem;
          width: #{$img-width}rem;
          height: 100rem;
          transform: translate(0, -50%);
          background-repeat: no-repeat;
          background-size: cover;
          background-position: center;
          z-index: 10;
          @include full {
            right: 66px;
            width: #{$img-width}px;
            height: 100px;
          }
          @include mobile {
            width: 150rem;
            height: 80rem;
            right: 0;
          }
          &--01 {
            background-image: url('/images/img_event_mass_01.png');
          }
          &--02 {
            background-image: url('/images/img_event_mass_02.png');
          }
          &--03 {
            background-image: url('/images/img_event_mass_03.png');
            background-position: 0 100%;
          }
          &--04 {
            background-image: url('/images/img_event_mass_04.png');
          }
        }
        @keyframes marquee {
          0% {
            transform: translate(0, 0);
          }
          100% {
            transform: translate(-100%, 0);
          }
        }
      }
    }
  }
}